/* Container for the entire chat component */
.chat-container {
    display: flex;
    height: 100vh;
    font-family: Arial, sans-serif;
  }
  
  /* Left column for patient data */
  .data-column {
    width: 35%;
    padding: 20px;
    border-right: 1px solid #ccc;
    overflow-y: auto;
  }
  
  .data-column h2 {
    margin-top: 0;
  }
  
  .data-column table {
    width: 100%;
    border-collapse: collapse;
  }
  
  .data-column .field-name {
    font-weight: bold;
    padding: 8px;
    background-color: #f5f5f5;
    width: 40%;
  }
  
  .data-column .field-value {
    padding: 8px;
    background-color: #fff;
  }
  
  /* Right column for chat interface */
  .chat-column {
    width: 65%;
    display: flex;
    flex-direction: column;
    padding: 20px;
  }
  
  /* Chat messages area */
  .chat-messages {
    flex: 1;
    overflow-y: auto;
    margin-bottom: 10px;
  }

  
  /* Individual chat message */
  .chat-message {
    display: flex;
    margin-bottom: 10px;
  }
  
  /* Message content styling */
  .message-content {
    max-width: 70%;
    padding: 10px;
    border-radius: 10px;
    font-size: 16px;
    line-height: 1.4;
  }
  
  /* User messages alignment and styling */
  .chat-message.user {
    justify-content: flex-end;
  }
  
  .chat-message.user .message-content {
    background-color: #dcf8c6;
    align-self: flex-end;
  }
  
  /* Bot messages alignment and styling */
  .chat-message.bot {
    justify-content: flex-start;
  }
  
  .chat-message.bot .message-content {
    background-color: #f1f0f0;
    align-self: flex-start;
  }
  
  /* Chat input area */
  .chat-input {
    display: flex;
    border-top: 1px solid #ccc;
    padding-top: 10px;
  }
  
  .chat-input input {
    flex: 1;
    padding: 10px;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .chat-input button {
    margin-left: 10px;
    padding: 10px 20px;
    font-size: 16px;
    background-color: #0084ff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .chat-input button:hover {
    background-color: #006bb3;
  }
  